        { value: 'chat', label: 'üí¨ ÂØπËØùAI' },
        { value: 'image', label: 'üé® ÂõæÂÉèÁîüÊàê' },
        { value: 'video', label: 'üé¨ ËßÜÈ¢ëÁîüÊàê' },
        { value: 'code', label: 'üíª ‰ª£Á†ÅÂä©Êâã' },
        { value: 'writing', label: '‚úçÔ∏è ÂÜô‰ΩúÂä©Êâã' },
        { value: 'design', label: 'üé® ËÆæËÆ°Â∑•ÂÖ∑' },
        { value: 'other', label: 'üîß ÂÖ∂‰ªñ' }
      ],
      toastMessage: '',
      showToast: false,
      toastType: 'success'
    }
  },
  computed: {
    categorizedWebsites() {
      if (!this.aiWebsites || !Array.isArray(this.aiWebsites)) {
        return {}
      }
      const categories = {}
      this.aiWebsites.forEach(site => {
        if (site && site.category) {
          if (!categories[site.category]) {
            categories[site.category] = []
          }
          categories[site.category].push(site)
        }
      })
      return categories
    },
    filteredWebsites() {
      if (!this.aiWebsites || !Array.isArray(this.aiWebsites)) {
        return []
      }
      let filtered = this.aiWebsites
      
      // ÂàÜÁ±ªÁ≠õÈÄâ
      if (this.selectedCategory) {
        filtered = filtered.filter(site => site.category === this.selectedCategory)
      }
      
      // ÊêúÁ¥¢Á≠õÈÄâ
      if (this.searchQuery && this.searchQuery.trim()) {
        const query = this.searchQuery.toLowerCase().trim()
        filtered = filtered.filter(site => 
          site.name.toLowerCase().includes(query) ||
          (site.description && site.description.toLowerCase().includes(query)) ||
          site.url.toLowerCase().includes(query)
        )
      }
      
      return filtered
    },
    filteredCategorizedWebsites() {
      if (!this.filteredWebsites || !Array.isArray(this.filteredWebsites)) {
        return {}
      }
      const categories = {}
      this.filteredWebsites.forEach(site => {
        if (site && site.category) {
          if (!categories[site.category]) {
            categories[site.category] = []
          }
          categories[site.category].push(site)
        }
      })
      return categories
    },
    filteredWebsitesCount() {
      return this.filteredWebsites ? this.filteredWebsites.length : 0
    },
    filteredCategoriesCount() {
      if (!this.filteredCategorizedWebsites || typeof this.filteredCategorizedWebsites !== 'object') {
        return 0
      }
      return Object.keys(this.filteredCategorizedWebsites).length
    }
  },
  mounted() {
    // ‰ªéÊú¨Âú∞Â≠òÂÇ®Âä†ËΩΩ‰øùÂ≠òÁöÑÁΩëÁ´ô
    const saved = localStorage.getItem('aiWebsites')
    if (saved) {
      try {
        const parsed = JSON.parse(saved)
        if (Array.isArray(parsed)) {
          this.aiWebsites = parsed
        }
      } catch (e) {
        console.error('Failed to load saved websites:', e)
      }
    }
  },
  methods: {
    openTool(toolName) {
      this.activeTool = toolName
      this.resetToolData(toolName)
    },
    closeTool() {
      this.activeTool = null
    },
    resetToolData(toolName) {
      // ÈáçÁΩÆÂêÑ‰∏™Â∑•ÂÖ∑ÁöÑÊï∞ÊçÆ
      if (toolName === 'summary') {
        this.summaryInput = ''
        this.summaryOutput = ''
      } else if (toolName === 'translate') {
        this.translateInput = ''
        this.translateOutput = ''
      } else if (toolName === 'generate') {
        this.generatePrompt = ''
        this.generateOutput = ''
      } else if (toolName === 'format') {
        this.codeInput = ''
        this.codeOutput = ''
      } else if (toolName === 'password') {
        this.passwordOutput = ''
      } else if (toolName === 'qrcode') {
        this.qrcodeInput = ''
        this.qrcodeOutput = null
      } else if (toolName === 'json') {
        this.jsonInput = ''
        this.jsonOutput = ''
        this.jsonError = ''
      } else if (toolName === 'color') {
        this.colorImageUrl = ''
        this.extractedColors = []
      } else if (toolName === 'websites') {
        // ‰∏çÈúÄË¶ÅÈáçÁΩÆ
      }
    },
    getToolTitle() {
      const titles = {
        summary: 'ÊñáÊú¨ÊëòË¶Å',
        translate: 'ÊñáÊú¨ÁøªËØë',
        generate: 'ÊñáÊú¨ÁîüÊàê',
        format: '‰ª£Á†ÅÊ†ºÂºèÂåñ',
        password: 'ÂØÜÁ†ÅÁîüÊàêÂô®',
        qrcode: '‰∫åÁª¥Á†ÅÁîüÊàê',
        json: 'JSONÊ†ºÂºèÂåñ',
        color: 'È¢úËâ≤ÊèêÂèñ',
        websites: 'AIÁΩëÁ´ôÊî∂ÈõÜ'
      }
      return titles[this.activeTool] || 'AIÂ∑•ÂÖ∑'
    },
    getCategoryName(category) {
      const names = {
        chat: 'üí¨ ÂØπËØùAI',
        image: 'üé® ÂõæÂÉèÁîüÊàê',
        video: 'üé¨ ËßÜÈ¢ëÁîüÊàê',
        code: 'üíª ‰ª£Á†ÅÂä©Êâã',
        writing: '‚úçÔ∏è ÂÜô‰ΩúÂä©Êâã',
        design: 'üé® ËÆæËÆ°Â∑•ÂÖ∑',
        other: 'üîß ÂÖ∂‰ªñÂ∑•ÂÖ∑'
      }
      return names[category] || category
    },
    addWebsite() {
      if (!this.newWebsite.name.trim() || !this.newWebsite.url.trim()) {
        this.showToastMessage('ËØ∑Â°´ÂÜôÁΩëÁ´ôÂêçÁß∞ÂíåURL', 'error')
        return
      }
      
      // È™åËØÅURLÊ†ºÂºè
      let url = this.newWebsite.url.trim()
      if (!url.startsWith('http://') && !url.startsWith('https://')) {
        url = 'https://' + url
      }
      
      try {
        new URL(url)
      } catch (e) {
        this.showToastMessage('ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑURL', 'error')
        return
      }
      
      // Ê£ÄÊü•ÊòØÂê¶Â∑≤Â≠òÂú®
      const exists = this.aiWebsites.some(site => 
        site.url === url || site.name.toLowerCase() === this.newWebsite.name.trim().toLowerCase()
      )
      
      if (exists) {
        this.showToastMessage('ËØ•ÁΩëÁ´ôÂ∑≤Â≠òÂú®', 'error')
        return
      }
      
      this.aiWebsites.push({
        name: this.newWebsite.name.trim(),
        url: url,
        description: this.newWebsite.description.trim(),
        category: this.newWebsite.category
      })
      
      // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
      localStorage.setItem('aiWebsites', JSON.stringify(this.aiWebsites))
      
      // ÈáçÁΩÆË°®Âçï
      this.newWebsite = {
        name: '',
        url: '',
        description: '',
        category: 'chat'
      }
      
      this.showAddForm = false
      this.showToastMessage('ÁΩëÁ´ôÊ∑ªÂä†ÊàêÂäüÔºÅ', 'success')
    },
    showToastMessage(message, type = 'success') {
      this.toastMessage = message
      this.toastType = type
      this.showToast = true
      setTimeout(() => {
        this.showToast = false
      }, 3000)
    },
    removeWebsite(category, index) {
      if (confirm('Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™ÁΩëÁ´ôÂêóÔºü')) {
        const sites = this.filteredCategorizedWebsites[category]
        const site = sites[index]
        const globalIndex = this.aiWebsites.findIndex(s => 
          s.name === site.name && s.url === site.url
        )
        if (globalIndex !== -1) {
          this.aiWebsites.splice(globalIndex, 1)
          localStorage.setItem('aiWebsites', JSON.stringify(this.aiWebsites))
          this.showToastMessage('ÁΩëÁ´ôÂ∑≤Âà†Èô§', 'success')
        }
      }
    },
    generateSummary() {
      if (!this.summaryInput.trim()) {
        alert('ËØ∑ËæìÂÖ•Ë¶ÅÊëòË¶ÅÁöÑÊñáÊú¨')
        return
      }
      // ÁÆÄÂçïÁöÑÊñáÊú¨ÊëòË¶ÅÁÆóÊ≥ïÔºàÊèêÂèñÂâçÂá†Âè•ÂíåÂÖ≥ÈîÆËØçÔºâ
      const sentences = this.summaryInput.split(/[„ÄÇÔºÅÔºü.!?]/).filter(s => s.trim())
      const summary = sentences.slice(0, Math.min(3, sentences.length)).join('„ÄÇ')
      this.summaryOutput = summary || this.summaryInput.substring(0, 200) + '...'
    },
    async translateText() {
      if (!this.translateInput.trim()) {
        alert('ËØ∑ËæìÂÖ•Ë¶ÅÁøªËØëÁöÑÊñáÊú¨')
        return
      }
      // ‰ΩøÁî®ÂÖçË¥πÁöÑÁøªËØëAPIÔºàËøôÈáå‰ΩøÁî®ÁÆÄÂçïÁöÑÁ§∫‰æãÔºåÂÆûÈôÖÂèØ‰ª•‰ΩøÁî®Google Translate APIÊàñÂÖ∂‰ªñÊúçÂä°Ôºâ
      try {
        // Ê≥®ÊÑèÔºöËøôÈáå‰ΩøÁî®‰∏Ä‰∏™ÁÆÄÂçïÁöÑÁøªËØëÊúçÂä°Á§∫‰æã
        // ÂÆûÈôÖÈ°πÁõÆ‰∏≠ÂèØ‰ª•‰ΩøÁî®Google Translate APIÊàñÂÖ∂‰ªñÁøªËØëÊúçÂä°
        const response = await fetch(`https://api.mymemory.translated.net/get?q=${encodeURIComponent(this.translateInput)}&langpair=${this.translateFrom}|${this.translateTo}`)
        const data = await response.json()
        if (data.responseData && data.responseData.translatedText) {
          this.translateOutput = data.responseData.translatedText
        } else {
          this.translateOutput = 'ÁøªËØëÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
        }
      } catch (error) {
        this.translateOutput = 'ÁøªËØëÊúçÂä°ÊöÇÊó∂‰∏çÂèØÁî®ÔºåËØ∑Á®çÂêéÈáçËØï'
        console.error('Translation error:', error)
      }
    },
    generateText() {
      if (!this.generatePrompt.trim()) {
        alert('ËØ∑ËæìÂÖ•ÊèêÁ§∫ËØç')
        return
      }
      // ÁÆÄÂçïÁöÑÊñáÊú¨ÁîüÊàêÔºàÂÆûÈôÖÈ°πÁõÆ‰∏≠ÂèØ‰ª•ÈõÜÊàêOpenAI APIÊàñÂÖ∂‰ªñAIÊúçÂä°Ôºâ
      const templates = {
        'ËØó': 'Êò•È£éÊãÇÈù¢Ëä±Êª°ÊûùÔºå\nÁªøÊÑèÁõéÁÑ∂È∏üËØ≠Êó∂„ÄÇ\n‰∏áÁâ©Â§çËãèÁîüÊú∫Áé∞Ôºå\nÁæéÂ•ΩÊó∂ÂÖâÊ≠£ÂΩìÊó∂„ÄÇ',
        'ÊñáÁ´†': `Ê†πÊçÆÊÇ®ÁöÑË¶ÅÊ±Ç"${this.generatePrompt}"Ôºå‰ª•‰∏ãÊòØÁõ∏ÂÖ≥ÂÜÖÂÆπÔºö\n\nËøôÊòØ‰∏Ä‰∏™Á§∫‰æãÊñáÊú¨ÁîüÊàê„ÄÇÂú®ÂÆûÈôÖÂ∫îÁî®‰∏≠ÔºåÊÇ®ÂèØ‰ª•ÈõÜÊàêOpenAI„ÄÅClaudeÁ≠âAIÊúçÂä°Êù•ÁîüÊàêÊõ¥È´òË¥®ÈáèÁöÑÂÜÖÂÆπ„ÄÇ`,
        'default': `Ê†πÊçÆÊèêÁ§∫"${this.generatePrompt}"Ôºå‰ª•‰∏ãÊòØÁîüÊàêÁöÑÂÜÖÂÆπÔºö\n\nËøôÊòØ‰∏Ä‰∏™Á§∫‰æãÊñáÊú¨„ÄÇÂú®ÂÆûÈôÖÈ°πÁõÆ‰∏≠ÔºåÂª∫ËÆÆÈõÜÊàê‰∏ì‰∏öÁöÑAIÊñáÊú¨ÁîüÊàêÊúçÂä°‰ª•Ëé∑ÂæóÊõ¥Â•ΩÁöÑÊïàÊûú„ÄÇ`
      }
      
      if (this.generatePrompt.includes('ËØó')) {
        this.generateOutput = templates['ËØó']
      } else if (this.generatePrompt.includes('ÊñáÁ´†')) {
        this.generateOutput = templates['ÊñáÁ´†']
      } else {
        this.generateOutput = templates['default']
      }
    },
    formatCode() {
      if (!this.codeInput.trim()) {
        alert('ËØ∑ËæìÂÖ•Ë¶ÅÊ†ºÂºèÂåñÁöÑ‰ª£Á†Å')
        return
      }
      try {
        if (this.codeLanguage === 'json') {
          const parsed = JSON.parse(this.codeInput)
          this.codeOutput = JSON.stringify(parsed, null, 2)
        } else {
          // ÁÆÄÂçïÁöÑ‰ª£Á†ÅÊ†ºÂºèÂåñÔºàÂÆûÈôÖÂèØ‰ª•‰ΩøÁî®PrettierÁ≠âÂ∑•ÂÖ∑Ôºâ
          this.codeOutput = this.codeInput
            .split('\n')
            .map(line => line.trim())
            .filter(line => line)
            .join('\n')
        }
      } catch (error) {
        alert('‰ª£Á†ÅÊ†ºÂºèÂåñÂ§±Ë¥•Ôºö' + error.message)
      }
    },
    generatePassword() {
      let charset = ''
      if (this.passwordOptions.uppercase) charset += 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
      if (this.passwordOptions.lowercase) charset += 'abcdefghijklmnopqrstuvwxyz'
      if (this.passwordOptions.numbers) charset += '0123456789'
      if (this.passwordOptions.symbols) charset += '!@#$%^&*()_+-=[]{}|;:,.<>?'
      
      if (!charset) {
        alert('ËØ∑Ëá≥Â∞ëÈÄâÊã©‰∏ÄÁßçÂ≠óÁ¨¶Á±ªÂûã')
        return
      }
      
      let password = ''
      for (let i = 0; i < this.passwordLength; i++) {
        password += charset.charAt(Math.floor(Math.random() * charset.length))
      }
      this.passwordOutput = password
    },
    generateQRCode() {
      if (!this.qrcodeInput.trim()) {
        alert('ËØ∑ËæìÂÖ•Ë¶ÅÁîüÊàê‰∫åÁª¥Á†ÅÁöÑÂÜÖÂÆπ')
        return
      }
      // ‰ΩøÁî®Âú®Á∫ø‰∫åÁª¥Á†ÅAPIÁîüÊàê
      const qrUrl = `https://api.qrserver.com/v1/create-qr-code/?size=300x300&data=${encodeURIComponent(this.qrcodeInput)}`
      this.qrcodeOutput = qrUrl
      
      // Âú®canvas‰∏äÁªòÂà∂‰∫åÁª¥Á†Å
      this.$nextTick(() => {
        const canvas = this.$refs.qrcodeCanvas
        if (canvas) {
          const ctx = canvas.getContext('2d')
          const img = new Image()
          img.crossOrigin = 'anonymous'
          img.onload = () => {
            canvas.width = 300
            canvas.height = 300
            ctx.drawImage(img, 0, 0)
          }
          img.src = qrUrl
        }
      })
    },
    downloadQRCode() {
      if (!this.qrcodeOutput) return
      const canvas = this.$refs.qrcodeCanvas
      if (canvas) {
        const link = document.createElement('a')
        link.download = 'qrcode.png'
        link.href = canvas.toDataURL()
        link.click()
      }
    },
    formatJSON() {
      if (!this.jsonInput.trim()) {
        alert('ËØ∑ËæìÂÖ•JSONÊï∞ÊçÆ')
        return
      }
      try {
        const parsed = JSON.parse(this.jsonInput)
        this.jsonOutput = JSON.stringify(parsed, null, 2)
        this.jsonError = ''
      } catch (error) {
        this.jsonError = 'JSONÊ†ºÂºèÈîôËØØÔºö' + error.message
        this.jsonOutput = ''
      }
    },
    handleImageUpload(event) {
      const file = event.target.files[0]
      if (file) {
        const reader = new FileReader()
        reader.onload = (e) => {
          this.colorImageUrl = e.target.result
        }
        reader.readAsDataURL(file)
      }
    },
    extractColors() {
      if (!this.colorImageUrl) return
      const img = new Image()
      img.crossOrigin = 'anonymous'
      img.onload = () => {
        const canvas = document.createElement('canvas')
        const ctx = canvas.getContext('2d')
        canvas.width = img.width
        canvas.height = img.height
        ctx.drawImage(img, 0, 0)
        
        // ÁÆÄÂçïÁöÑÈ¢úËâ≤ÊèêÂèñÁÆóÊ≥ïÔºàÊèêÂèñ‰∏ªË¶ÅÈ¢úËâ≤Ôºâ
        const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height)
        const colors = {}
        const sampleSize = 1000 // ÈááÊ†∑Êï∞Èáè
        
        for (let i = 0; i < sampleSize; i++) {
          const index = Math.floor(Math.random() * imageData.data.length / 4) * 4
          const r = imageData.data[index]
          const g = imageData.data[index + 1]
          const b = imageData.data[index + 2]
          const color = `rgb(${r}, ${g}, ${b})`
          colors[color] = (colors[color] || 0) + 1
        }
        
        // Ëé∑ÂèñÊúÄÂ∏∏ËßÅÁöÑÂá†ÁßçÈ¢úËâ≤
        const sortedColors = Object.entries(colors)
          .sort((a, b) => b[1] - a[1])
          .slice(0, 8)
          .map(([color]) => color)
        
        this.extractedColors = sortedColors
      }
      img.src = this.colorImageUrl
    },
    copyToClipboard(text) {
      navigator.clipboard.writeText(text).then(() => {
        alert('Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø')
      }).catch(() => {
        // Â§áÁî®ÊñπÊ°à
        const textarea = document.createElement('textarea')
        textarea.value = text
        document.body.appendChild(textarea)
        textarea.select()
        document.execCommand('copy')
        document.body.removeChild(textarea)
        alert('Â∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø')
      })
    }
  }
}
</script>

